
import { defineComponent, onMounted, ref, watchEffect, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import Chart from 'chart.js/auto'
import axios from 'axios';
import {mask} from 'vue-the-mask'

type pacient = {
  treatment: string,
  measurements?: measure[],
  tonometr: string
}

type measure = {
  upper: number,
  bottom: number,
  pulse: number,
  commnet?: string,
  date: string
}

export default defineComponent({
  directives: {mask},
  setup() {

    const route = useRoute();
    const router = useRouter();
    const apiPatient = '/api/patient_by_snils/'
    const apiTreatment = '/api/add_treatment/'
    const apiNote = '/api/add_note/'
    const snils = ref('')
    
    const fname = ref('')
    const lname = ref('')
    const age = ref('')
    const gender = ref('')
    const height = ref('')
    const weight = ref('')
    const phone = ref('')
    const treatment_start = ref('')
    const treatment = ref([])
    const measurements = ref([])
    const tonometer = ref('')
    const notes = ref('')
    const graphType = ref('all')
    const startDate = ref('')
    const endDate = ref('')
    const treatmentCurrent = ref('')
    const isNotFound = ref(0)
    

    const dataset = ref([
      {
        label: 'Верхнее давление',
        data: [] as number[],
        borderColor: '#FF5151',
        backgroundColor: '#FF5151',
        fill: false,
      },
      {
        label: 'Нижнее давление',
        data: [] as number[],
        borderColor: '#08B408',
        backgroundColor: '#08B408',
        fill: false,
      },
      {
        label: 'Пульс',
        data: [] as number[],
        borderColor: '#0082FF',
        backgroundColor: '#0082FF',
        fill: false,
      }
    ])

    const data = ref({
    labels: [] as string[],
    datasets: graphType.value == 'all' ? dataset.value :
    graphType.value == 'systolic' ? [dataset.value[0]] :
    graphType.value == 'diastolic' ? [dataset.value[1]]:
    [dataset.value[2]]
  })

  watchEffect(() => {
    data.value.datasets = graphType.value == 'all' ? dataset.value :
    graphType.value == 'systolic' ? [dataset.value[0]] :
    graphType.value == 'diastolic' ? [dataset.value[1]]:
    [dataset.value[2]]
  })

    const config = ref({
      type: 'line',
      data: data.value,
      options: {
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                padding: 25
              }
            }
          ]
        },
        interaction: {
          intersect: false,
        },
        lineTension: .35,
      },
    });

    // watchEffect(graphType => {
    //   const ctx = document.getElementById('graph')
    //   new Chart(ctx, config.value)
    // })


      
      let chart: any;

      onMounted(() => {
        const ctx = document.getElementById('graph')
        chart = new Chart(ctx, config.value)
      })

      watch(config.value, () => {
        chart.update()
      })

    const getPatient = async () => {
      console.log(route.params.sault);
      
      await axios.post(
        apiPatient,
        {
          "snils": snils.value,
          "sault": route.params.sault,
        }
      ).then(response => {
        isNotFound.value = 0
        fname.value = response.data.first_name
        lname.value = response.data.last_name
        age.value = response.data.age
        phone.value = response.data.phone
        gender.value = response.data.gender
        height.value = response.data.height
        weight.value = response.data.weight
        treatment_start.value = response.data.treatment_start
        treatment.value = response.data.treatment
        measurements.value = response.data.measurements
        tonometer.value = response.data.tonometer
        notes.value = response.data.notes

        let labels = [] as string[];
        let upper = [] as number[];
        let bottom = [] as number[];
        let pulse = [] as number[];

        measurements.value.forEach((m: measure) => {
          labels.push((new Date(m.date)).toLocaleDateString())
          upper.push(m.upper)        
          bottom.push(m.bottom)
          pulse.push(m.pulse)
        })

        data.value.labels = labels;
        dataset.value[0].data = upper;
        dataset.value[1].data = bottom;
        dataset.value[2].data = pulse;
      }).catch(error => {
        isNotFound.value = 1
      })
    }

    const addTreatment = () => {
      axios.post(
        apiTreatment,
        {
          "comment": treatmentCurrent.value,
          "snils": snils.value,
          "sault": route.params.sault,
        }
      ).catch(e => alert(e))
      getPatient();
    }

    const addNote = () => {
      axios.post(
        apiNote,
        {
          "text": notes.value,
          "snils": snils.value,
          "sault": route.params.sault,
        }
      ).catch(e => alert(e))
      getPatient();
    }

    return {
      snils,
      getPatient,
      fname,
      lname,
      age,
      gender,
      height,
      weight,
      treatment_start,
      treatment,
      measurements,
      tonometer,
      notes,
      graphType,
      startDate,
      endDate,
      phone,
      treatmentCurrent,
      addNote,
      addTreatment,
      dataset,
      isNotFound
    }
  },
})
